@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);

.SearchModule{
  background: rgb(218, 221, 218);
  width:95%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 24px;
  transition: 1s ease;
}

.App{
    font-family: Roboto;
    display: flex;
    margin: 0 auto;
    width:100%;
}
.search-bar{
    border-radius: 4px;
    padding: 8px;
    margin-bottom: 24px;
}

.error-message{
    
    color: red;
    font-size: 18px;
}
.SearchResultBoard{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100vh;
    background: rgb(218, 221, 218);
    margin: 16px;
}
.pokemon-card{
    background: white;
    margin: 8px;
    border-radius:12px;
    padding: 8px;
    max-height: 220px;
    min-width: 160px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 5px 5px 5px 0px rgb(188, 209, 171);
}

.pokemon-card__name{
    margin:4px 0 8px 0;
    font-size: 18px;
    font-weight: bold;
}

.pokemon-card__type{
    margin:0 0 24px 0;
    color: rgb(110, 110, 228);
}

.pokemon-card__save-button{
    border-radius: 12px;
    color:rgb(255, 255, 255);
    background:rgb(110, 110, 228);
    padding: 8px;
    border:none;
    cursor: pointer;
}
.Pokedex {
    background: red;
    position: relative;
    transition: 1s ease;
    height: 100vh;
    display: flex;
    flex-direction: column;
    /* align-items: flex-start; */
    padding: 8px;
    width: 15%;
    /* overflow: hidden; */
}

.Pokedex--hidden {
    width: 0%;
    padding: 0;
    /* overflow: hidden; */
}

.Pokedex__list {
    list-style-type: none;
    list-style-position: inside;
    list-style-image: none;
    padding: 0 0 0 16px;
}

.Pokedex__header {
    margin: 0 auto;
    font-size: 20px;
    width: 100%;
    border-bottom: 1px solid black;
}

.Pokedex__button {
    position: absolute;
    border: none;
    height: 40px;
    font-size: 20px;
    border-radius: 5%;
    box-shadow: 5px 5px 5px 0px rgb(188, 209, 171);
    left: 100%;
    top: 2rem;
    cursor: pointer;
}

